import React, { useEffect, useState } from "react";
import axios from "axios";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";
import parse from "html-react-parser";
import Cookies from "js-cookie";
import CryptoJS from "crypto-js";
const Testimonial = () => {
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    : null;
  const [data1, setdata1] = useState([]);
  const getTestimonials = async () => {
    try {
      const res = await axios.get(
        "https://majesticmall.net/api/admin/getTestimonials",
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        setdata1(res.data.testimonials);
      }
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    getTestimonials();
  }, []);

  const chunkArray = (array, size) => {
    const result = [];
    for (let i = 0; i < array.length; i += size) {
      result.push(array.slice(i, i + size));
    }
    return result;
  };

  // const testimonialChunks = chunkArray(data1, 2);

  const [chunkSize, setChunkSize] = useState(window.innerWidth > 768 ? 2 : 1);
  const [testimonialChunks, setTestimonialChunks] = useState(
    chunkArray(data1, chunkSize)
  );

  // Function to update chunk size on screen resize
  useEffect(() => {
    const updateChunkSize = () => {
      const newSize = window.innerWidth > 768 ? 2 : 1; // Large screen: 2, Small screen: 1
      setChunkSize(newSize);
      setTestimonialChunks(chunkArray(data1, newSize));
    };

    updateChunkSize(); // Run on mount

    window.addEventListener("resize", updateChunkSize);
    return () => window.removeEventListener("resize", updateChunkSize);
  }, [data1]); // Re-run when `data1` changes

  return (
    <div className="mt-10 mx-4 sm:mx-20 textmonial">
      {/* <p className='text-center font-semibold text-xl lg:text-2xl' style={{color: '#000'}}>Our Testimonial</p>
            <h1 className='text-center font-bold text-3xl mt-2 md:text-4xl lg:text-5xl' style={{color: '#45595b'}}>Our Client Saying!</h1> */}

      <p className="text-[16px] uppercase text-center m-0 p-0  text-gradient font-extrabold quicksand">
        Our Testimonial
      </p>
      <h1
        className="text-2xl md:text-4xl lg:text-4xl font-bold text-center quicksand mt-1 text-[#000]"
        // style={{ color: "#45595b" }} a3d04f
      >
        Our Client Saying!
      </h1>
      <div className="flex justify-center items-center m-0 p-0 mb-4">
        <span className="w-[15%] h-1 gradient1 rounded-md m-0 p-0"></span>
      </div>

      <Carousel
        showArrows={true}
        infiniteLoop={true}
        showIndicators={false}
        autoPlay={true}
        interval={5000}
        showThumbs={false}
        className="mt-10"
        emulateTouch={true}
        swipeable={true}
      >
        {testimonialChunks.map((chunk, index) => (
          <div
            key={index}
            className="flex flex-col md:flex-row md:gap-3 justify-center items-center"
          >
            {chunk.map((val) => (
              <div
                key={val.id}
                className="rounded-lg w-full md:w-1/2"
                style={{ backgroundColor: "#f4f6f8" }}
              >
                <div className="flex flex-col md:flex-row justify-between items-start p-4">
                  <div className="flex  mb-4 md:mb-0 gap-4 justify-start">
                    <img
                      className="w-12 h-12 md:w-16 md:h-16 rounded-full"
                      src={
                        val?.profileImage
                          ? `https://majesticmall.net/image/${val?.profileImage}`
                          : "./img/dummy-avatar.jpg"
                      }
                      alt="profile ims"
                      loading="lazy"
                    />
                    <div className="">
                      <h3
                        className="text-lg font-semibold m-0 p-0 truncate"
                        style={{ color: "#45595b" }}
                      >
                        {val.name}
                      </h3>
                      <p
                        className="text-s p-0 m-0"
                        style={{ color: "#45595b" }}
                      >
                        {val.profession}
                      </p>
                      <ul className="flex m-0 p-0">
                        {[...Array(5)].map((_, index) => (
                          <span
                            key={index}
                            style={{
                              color: index < val.rating ? "#000" : "#45595b",
                            }}
                          >
                            &#9733;
                          </span>
                        ))}
                      </ul>
                    </div>
                  </div>

                  <div className="flex-1 text-center md:text-left">
                    <div className="flex justify-center md:justify-end">
                      <span className="flex justify-center items-center">
                        <i
                          className="fa fa-quote-right fa-2x"
                          style={{ color: "#ffb524" }}
                        ></i>
                      </span>
                    </div>
                    <p
                      className="mt-4 md:ml-6 line-clamp-5 text-justify"
                      style={{ color: "#45595b" }}
                    >
                      {parse(val.message)}
                    </p>
                  </div>
                </div>

                {/* <hr className='w-10/12 m-auto h-0.5 xl:h-1' style={{ backgroundColor: '#ffb524' }} /> */}
              </div>
            ))}
          </div>
        ))}
      </Carousel>
    </div>
  );
};

export default Testimonial;
